import { useState } from "react";
import { FaChevronLeft } from "react-icons/fa";
import CheckBox from "./CheckBox";
import RadioInput from "./RadioInput";
import { AnimatePresence, motion } from "framer-motion";
import { RiEqualizerFill } from "react-icons/ri";
import { MdSort } from "react-icons/md";

const variants = {
  appear: {
    opacity: 0,
    x: "100%",
    transition: {
      type: "spring",
      stiffness: 400,
      damping: 60,
    },
  },
  open: {
    opacity: 1,
    x: 0,
    transition: {
      type: "spring",
      stiffness: 400,
      damping: 60,
    },
  },
  closed: {
    opacity: 0,
    x: "100%",
    transition: {
      type: "spring",
      stiffness: 400,
      damping: 40,
    },
  },
};

const backdropVariants = {
  initial: { opacity: 0 },
  animate: { opacity: 1 },
  exit: { opacity: 0 },
};

const swipeConfidenceThreshold = 10000;
const swipePower = (offset, velocity) => {
  return Math.abs(offset) * velocity;
};

const Backdrop = ({ children, onClick }) => {
  return (
    <motion.div
      variants={backdropVariants}
      initial="initial"
      animate="animate"
      exit="exit"
      onClick={onClick}
      className="SIDEBAR-BACKDROP fixed z-50 bg-black bg-opacity-80 inset-0 overflow-hidden"
    >
      {children}
    </motion.div>
  );
};

const Sidebar = ({ isOpen, onClose, children }) => {
  return (
    <AnimatePresence>
      {isOpen && (
        <Backdrop onClick={onClose}>
          <motion.div
            initial="appear"
            animate="open"
            exit="closed"
            variants={variants}
            drag="x"
            dragConstraints={{ left: 0, right: 0 }}
            dragElastic={{ left: 0, right: 1 }}
            dragMomentum={false}
            dragTransition={{ delay: -0.5 }}
            onDragEnd={(e, { offset, velocity }) => {
              const swipe = swipePower(offset.x, velocity.x);
              const isRightDirection = offset.x > 45 && velocity.x >= 0;
              if (swipe > swipeConfidenceThreshold || isRightDirection) {
                onClose();
              }
            }}
            onClick={(e) => e.stopPropagation()}
            className="h-svh max-w-96 w-full absolute top-0 right-0"
          >
            {children}
          </motion.div>
        </Backdrop>
      )}
    </AnimatePresence>
  );
};

const MobileFilter = ({
  filters,
  filterConfig,
  onFilterChange,
  onClearFilters,
}) => {
  const [isSortByModalOpen, setSortByModalOpen] = useState(false);
  const [isFilterModalOpen, setFilterModalOpen] = useState(false);

  return (
    <div className="w-full flex items-center gap-4">
      {/* Button for "Sort By" Sidebar */}
      <FilterButton
        title="Sort By"
        icon={<MdSort />}
        onClick={() => setSortByModalOpen(true)}
      />
      {/* Button for "Filter" Sidebar */}
      <FilterButton
        title="Filter"
        icon={<RiEqualizerFill />}
        onClick={() => setFilterModalOpen(true)}
      />

      {/* Sidebar for "Sort By" */}
      <Sidebar
        key="sortBy"
        isOpen={isSortByModalOpen}
        onClose={() => setSortByModalOpen(false)}
      >
        <SortBySidebar
          filters={filters}
          filterConfig={filterConfig.filter(({ key }) => key === "sortBy")}
          onFilterChange={onFilterChange}
          onClose={() => setSortByModalOpen(false)}
        />
      </Sidebar>
      {/* Sidebar for "Filter" */}
      <Sidebar
        key="filter"
        isOpen={isFilterModalOpen}
        onClose={() => setFilterModalOpen(false)}
      >
        <FilterSidebar
          filters={filters}
          filterConfig={filterConfig.filter(({ key }) => key !== "sortBy")}
          onFilterChange={onFilterChange}
          onClearFilters={onClearFilters}
          onClose={() => setFilterModalOpen(false)}
        />
      </Sidebar>
    </div>
  );
};

const FilterButton = ({ title, icon, onClick }) => (
  <button
    type="button"
    className="flex items-center justify-center gap-2 border border-primary rounded-full px-2 py-1"
    onClick={onClick}
  >
    <p className="text-sm">{title}</p>
    <span className="text-sm">{icon}</span>
  </button>
);

const SortBySidebar = ({ filters, filterConfig, onFilterChange, onClose }) => {
  const sortByConfig = filterConfig.find((config) => config.key === "sortBy");

  const handleRadioChange = (filterKey, value) => {
    onFilterChange(filterKey, value);
  };

  return (
    <div className="bg-white h-dvh">
      <div className="HEADING px-4 py-2 border-b border-primary flex items-center gap-2">
        <FaChevronLeft onClick={onClose} className="cursor-pointer" />
        <p>Sort By</p>
      </div>
      <div className="p-4">
        <form className="space-y-2">
          {sortByConfig?.options.map((option) => (
            <RadioInput
              key={option.value}
              label={option.label}
              name={sortByConfig.key}
              value={option.value}
              checked={filters[sortByConfig.key] === option.value}
              onChange={() => handleRadioChange(sortByConfig.key, option.value)}
              labelClassName="text-sm capitalize"
            />
          ))}
        </form>
      </div>
    </div>
  );
};

const FilterSidebar = ({
  filters,
  filterConfig,
  onFilterChange,
  onClearFilters,
  onClose,
}) => {
  const [activeTab, setActiveTab] = useState(filterConfig[0]?.header);

  // Define the function to handle checkbox changes
  const handleCheckboxChange = (filterKey, value) => {
    const currentValues = filters[filterKey]?.split(",").filter(Boolean) || [];
    const newValues = currentValues.includes(value)
      ? currentValues.filter((v) => v !== value) // Remove if already selected
      : [...currentValues, value]; // Add if not selected

    onFilterChange(filterKey, newValues.join(","));
  };

  const handleRadioChange = (filterKey, value) => {
    onFilterChange(filterKey, value);
  };

  const renderFilterOptions = (config) => {
    return (
      <div className="p-4">
        <form className="space-y-2">
          {config.type === "radio" &&
            config.options.map((option) => (
              <RadioInput
                key={option.value}
                label={option.label}
                name={config.key}
                value={option.value}
                checked={filters[config.key] === option.value}
                onChange={() => handleRadioChange(config.key, option.value)}
                labelClassName="text-sm capitalize"
              />
            ))}
          {config.type === "checkbox" &&
            config.options.map((option) => (
              <CheckBox
                key={option.value}
                label={option.label}
                name={config.key}
                value={option.value}
                checked={filters[config.key]?.split(",").includes(option.value)}
                onChange={() => handleCheckboxChange(config.key, option.value)}
                labelClassName="text-sm"
              />
            ))}
        </form>
      </div>
    );
  };

  return (
    <div className="bg-white h-dvh relative flex flex-col">
      <div className="HEADING px-4 py-2 border-b border-primary flex items-center gap-2">
        <FaChevronLeft onClick={onClose} className="cursor-pointer" />
        <p>Filters</p>
      </div>
      <div className="BODY h-full flex">
        <div className="LEFT w-2/5 h-full bg-lightGray">
          <ul>
            {filterConfig.map((tab) => (
              <li
                key={tab.key}
                onClick={() => setActiveTab(tab.header)}
                className={`px-4 py-2 cursor-pointer ${
                  activeTab === tab.header && "bg-white text-blue-400"
                }`}
              >
                <p className="text-sm whitespace-nowrap">{tab.header}</p>
              </li>
            ))}
          </ul>
        </div>
        <div className="RIGHT w-3/5 h-full">
          {renderFilterOptions(
            filterConfig.find((config) => config.header === activeTab)
          )}
        </div>
      </div>
      <div className="absolute bottom-0 right-0 left-0 bg-white flex justify-between py-2 px-4">
        <button
          type="button"
          className="px-4 py-2 text-sm text-error disabled:text-transparent"
          onClick={onClearFilters}
          disabled={
            !filterConfig.some(
              (config) => filters[config.key] && filters[config.key] !== ""
            )
          }
        >
          Clear Filters
        </button>
        <button
          type="button"
          className="px-4 py-2 rounded text-sm text-white bg-blue-500 hover:bg-blue-600"
          onClick={onClose}
        >
          Apply
        </button>
      </div>
    </div>
  );
};

export default MobileFilter;
