import { useEffect, useRef, useState } from "react";
import { useBanners } from "../services/bannerServices";
import Carousel from "./Carousel";
import Skeleton from "./Skeleton";
import useInView from "../hooks/useInView";
import useMediaQuery from "../hooks/useMediaQuery";

const LeaderBoard = ({
  preload = false,
  position,
  resourceType,
  valueId,
  title,
  subtitle,
  className,
  containerClassName = "container max-w-full",
}) => {
  const containerRef = useRef(null);
  const hasFetchedOnce = useRef(false);
  const [shouldFetch, setShouldFetch] = useState(preload);
  const maxMd = useMediaQuery("(max-width: 768px)");

  const isInView = useInView(containerRef, {
    threshold: 0.1,
    rootMargin: "100px",
  });

  const { data: banners = [], isLoading } = useBanners(
    {
      bannerType: "Leaderboard",
      position,
      resourceType,
      valueId,
    },
    { enabled: shouldFetch }
  );

  useEffect(() => {
    if (!hasFetchedOnce.current && isInView && position && resourceType) {
      setShouldFetch(true);
      hasFetchedOnce.current = true;
    }
  }, [isInView, position, resourceType]);

  return (
    <div ref={containerRef}>
      {isLoading ? (
        <ul className={`flex gap-4 overflow-x-hidden ${containerClassName}`}>
          {Array.from({ length: 1 }).map((_, index) => (
            <li
              key={index}
              className={`block relative w-full h-30 max-lg:h-20 max-md:h-10 overflow-hidden flex-shrink-0 ${className}`}
            >
              <Skeleton />
            </li>
          ))}
        </ul>
      ) : (
        banners?.length > 0 && (
          <div className={`space-y-4 max-lg::space-y-2 ${containerClassName}`}>
            {title || subtitle ? (
              <div>
                <h1 className="text-2xl max-lg:text-xl font-bold">{title}</h1>
                <p className="text-sm">{subtitle}</p>
              </div>
            ) : null}
            <Carousel autoplay delay={5000} navigation={!maxMd} pagination>
              {banners.map((banner) => (
                <a
                  key={banner.bannerTitle || banner._id}
                  href={banner.linkUrl}
                  className="block relative w-full h-max"
                >
                  <img
                    src={banner.bannerImg}
                    alt={banner.bannerTitle || banner._id}
                    loading="lazy"
                    className={`w-full object-cover object-center ${className}`}
                  />
                </a>
              ))}
            </Carousel>
          </div>
        )
      )}
    </div>
  );
};

export default LeaderBoard;
